import {oneOf} from "../../utils/utils";
export default {
  name: 'myBlockListChildTree',
  props: {
    selValue: {
      default: function () {
        return {};
      },
      type: Object
    },
    size: {
      defalult: 'small',
      validate: function (val) {
        return oneOf(val, ['large', 'medium', 'small', 'mini']);
      }
    },
    width: {
      default: '',
      type: [String, Number]
    },
    className: {
      defalult: '',
      type: [String, Number]
    },
    iconExtra: {
      default: '',
      type: String
    },
    iconPrefix: {
      default: '',
      type: String
    },
    opr: {
      default: false,
      type: Boolean
    }
  },
  computed: {
    selModel(){
      this.value = this.selValue;
    },
    setClass(){
      return [
        {
          ['tree-small']: !this.size,
          [`tree-${this.size}`]: !!this.size,
        }
      ]
    },
    setWidth() {
      if (this.width && this.width != ""){
        //this.widthStyle['width'] = this.width - 80 + "px";
      }
    }
  },
  data() {
    return {
      value: '',
      valueGroup: [],
      widthStyle: {},
      widthItemStyle: {},
      widthItemChildStyle: {},
      iconExtraShow: false,
      oprShow: false,
      dataCollege: []
    }
  },
  mounted() {
    this.oprShow = this.opr;
  },
  methods: {
    handleChange(data) {
      this.$emit('click', data);
    },
    handleEdit(event, data) {
      this.$emit('click', data);
    },
    handleDelete(event, data) {
      this.$emit('click', data);
    },
    changeToggle(event, data) {
      data.show = !data.show;
      this.$emit('click', data);
    },
    setPLeft(){
      console.log(this.$el);
    }
  }
}
